@import url(https://fonts.googleapis.com/css2?family=Cinzel&family=Dancing+Script&family=Dosis&family=Tourney:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.intro {
  margin-left: 4%;
  margin-top: 1%;
  width: 30%;
}

.bar-holder > div {
  height: 10px;
  margin: 10px 0;
}
.bar1 {
  width: 90%;
  background: red;
}
.bar2 {
  width: 95%;
  background: blue;
}
.bar3 {
  width: 100%;
  background: green;
}
.intro > h1 {
  font-family: var(--ff-primary);
  font-size: var(--fs-h1);
}
.intro > h2 {
  font-size: var(--fs-h2);
  font-family: var(--ff-second);
}

.content-holder {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.tabs {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  width: 50%;
}

.bar {
  padding: 10px;
  height: 60px;
  width: 80%;
  align-self: flex-end;
  margin-bottom: 7%;
}

.about-bar {
  background-color: green;
}

.experience-bar {
  background-color: yellow;
}
.projects-bar {
  background-color: lightblue;
}

.resume-bar {
  background-color: orange;
}

.contact-bar {
  background-color: white;
}

.info-holder {
  margin-right: auto;
  margin-left: 1%;
  height: 100%;
  width: 100%;
}

.fadeOut {
  background-color: yellow;
  opacity: 0;
  width: 0;
  height: 0;
  transition: width 0.5s 0.5s, height 0.5s 0.5s, opacity 0.5s;
}
.fadeIn {
  background-color: green;
  opacity: 1;
  width: 70%;
  height: 100px;
  transition: width 0.5s, height 0.5s, opacity 0.5s 0.5s;
}

.info {
  display: block;
  padding: 1%;
  background-color: rgb(245, 245, 220);
  border-radius: 25px;
  overflow-y: auto;
  border-style: solid;
  height: 600px;
  border-color: bisque;
}


.info>h1{
  
    font-family: var(--ff-text);
    font-size: 1.5rem;

}
.about{
    font-family: var(--ff-text);
    font-size: 1.5rem;
}
.about > h1 {
  font-family: var(--ff-cursive);
  font-size: var(--fs-h1);
}

.experience {
  font-family: var(--ff-text);
  font-size: 1.5rem;
}
.experience > h1 {
  font-family: var(--ff-cursive);
  font-size: 4.3rem;
}

.exp-holder > h2 {
  font-size: 1.5rem;
  line-height: 1;
  margin: 0;
}

.exp-holder > hr {
  margin: 0;
}
.exp-holder > p {
  margin: 3px;
  
}

.projects {
  font-family: var(--ff-text);
  font-size: 1.5rem;
}
.projects > h1 {
  font-family: var(--ff-cursive);
  font-size: 4.3rem;
}

.proj-holder > h2 {
  font-size: 2rem;
  line-height: 1;
  margin: 0;
}

.proj-holder > hr {
  margin: 0;
}
.ptag {
  margin: 5px 0;
}

.bigger {
  font-size: 1.5rem;
}

.smaller {
  font-size: 1.2rem;
}

.contact {
    font-family: var(--ff-text);
    font-size: 1.5rem;
  }
  .contact > h1 {
    font-family: var(--ff-cursive);
    font-size: 4.3rem;
  }
.resume {
    font-family: var(--ff-text);
    font-size: 1.5rem;
  }
  .resume > h1 {
    font-family: var(--ff-cursive);
    font-size: 4.3rem;
  }
  
  .resume > h2 {
    font-size: 2rem;
    line-height: 1;
    margin: 0;
  }

  
html,
body {
  height: 100%;
  box-sizing: border-box;
  max-width: 100%;
  overflow-x: hidden;
}

body {
  background: #43cea2;
  background: linear-gradient(to right, #43cea2, #185a9d);
}

h1,
h2,
h3 {
  line-height: 1;
  margin: 1px;
}
h3{
  font-size: 1.4rem;
}
a {
  color: blue;
  text-decoration: none;
}
ul{
  margin:0;
  margin-top: 3px;
  margin-bottom: 30px;
}

:root {
  --ff-primary: "Cinzel", serif;
  --ff-second: "Tourney", cursive;

  --ff-text: "Dosis", sans-serif;
  --ff-cursive: "Dancing Script", cursive;

  --fw-reg: 300;
  --fw-bold: 900;

  --clr-light: #fff;
  --clr-dark: #303030;
  --clr-accent: #16e0bd;

  --fs-h1: 4.3rem;
  --fs-h2: 2.25rem;
  --fs-h3: 1.5rem;
  --fs-p: 2rem;
  --fs-body: 1rem;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

